import React from 'react';



function Card() {
    const sup = _ => {
        console.log('hello');
    }

    const text = 'Read More'

    return (
        <div className="card mb-3">
            <div className="row">
                <div className="col-md-6 info">
                    <div className="card-body more-info">
                        <h2 className="card-title">Hello<br /></h2>
                        <p className='card-text'>Hello</p>
                    </div>
                </div>
                <div className="col-md-6 img-div">
                    <img src='./nicelogo.png' alt='broke' className='card-img'/>
                </div>
                <div className="button-div mt-3">
                    <a className="btn btn-info stretched-link" href='https://google.com' onClick={sup}>{text} <i className="fa fa-angle-right" aria-hidden="true"> </i><i className="fa fa-angle-right" aria-hidden="true"> </i> </a>
                </div>
            </div>
        </div>
    );
}

export default Card;